.collections{text-align:center}.collections .tabs{display:flex;flex-wrap:wrap;justify-content:center;gap:24px;margin:16px 0}.collections .tabs button{font-size:20px;line-height:48px;padding:0px 24px;background:none;border:none;cursor:pointer}.collections .tabs button:first-child{background-image:linear-gradient(45deg,#ffffff,#000000);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text;text-emphasis-color:transparent}.collections .tabs button:last-child{background-image:linear-gradient(45deg,#000000,#ffffff);-webkit-background-clip:text;-webkit-text-fill-color:transparent;background-clip:text;text-emphasis-color:transparent}.collections .tabs button:hover{color:#C6A26D;-webkit-background-clip:initial;-webkit-text-fill-color:initial;background-clip:initial;background-image:none}.collections .grid{display:grid;grid-template-columns:1fr;gap:16px;width:1200px}.collections .grid .grid-item{grid-column:span 2}.collections .grid img{width:100%;object-fit:cover}